<template>
    <div>
        <g-appbar
            title="한국전력공사  T.B.M" 
            background="#f8f8fa"
            :left="true" :right="true"
        />
        <div class="g-area-top">
            <div class="d-flex align-center">
                <p class="g-step-now">{{ step }}</p>
                <div class="g-step-bar"></div>
                <p class="g-step">{{ totalStep }}</p>
            </div>
            <p class="s-title">
                중점관리 위험 요인을 <br/>
                확인해 주세요.
            </p>
        </div>
        <div class="g-progress">
            <v-progress-linear 
                :model-value="step"
                :buffer-value="progress"
                color="#7285f3"
                background-opacity="1"
            ></v-progress-linear>
        </div>
        <div class="g-area-middle">
            <div class="g-voice">
                <div class="s-notice-box">
                    <img class="s-notice-icon" src="@/assets/images/icon/i_speaker.png"/>
                    <p>
                        아래의 <span class="font_bold">전달 사항</span>을 소리 내어 
                        <span class="font_bold"> 말씀해 주세요.</span>
                    </p>
                </div>
                <div class="s-content">
                    <span v-for="(item, index) in textArray" :key="index" class="ml-1">
                        <span :class="textStyle(item)" v-html="item.text"></span>
                    </span>
                </div>
            </div>
            <div class="g-fixed-voice-btn">
                <div class="g-fixed-voice-gradation">
                    <img class="g-fixed-voice-gradation" src="@/assets/images/voice/i_gradation_top.png"/>
                </div>
                <VoiceRecorderHidden 
                    ref="VoiceRecorder"
                    @checkAnalysisResult="checkAnalysisResult"
                    @midResultState="midResultState"
                />
                <basic-btn 
                    :state="voiceState"
                    @onClick="onClickNext"
                />
                <div v-if="voiceState === 'replay'">
                    <next-btn @onClick="onForceNext"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BasicBtn from '@/components/button/BasicBtn.vue'
import VoiceRecorderHidden from '@/components/common/VoiceRecorderHidden.vue'
import NextBtn from '@/components/button/NextBtn.vue'

export default {
    name: 'Step01',
    components: { BasicBtn, VoiceRecorderHidden, NextBtn },
    data() {
        return {
            step: 5,
            totalStep: 6,
            textArray: [
                { index: 1, text: '금일 ', check: true, require: false },
                { index: 2, text: '작업의 ', check: true, require: false },
                { index: 3, text: '중점 ', check: false, require: true },
                { index: 4, text: '관리 ', check: false, require: true },
                { index: 5, text: '사항은 ', check: false, require: true },
                { index: 6, text: '차량 ', check: false, require: true },
                { index: 7, text: '끼임 ', check: false, require: true },
                { index: 8, text: '방지입니다. ', check: true, require: false },
                { index: 9, text: '과거 ', check: true, require: false },
                { index: 10, text: '고임목 ', check: false, require: true },
                { index: 11, text: '및 ', check: true, require: false },
                { index: 12, text: '아웃 ', check: false, require: true },
                { index: 13, text: '트리거 ', check: false, require: true },
                { index: 14, text: '조작 ', check: true, require: false },
                { index: 15, text: '시 ', check: true, require: false },
                { index: 16, text: '차량 ', check: true, require: false },
                { index: 17, text: '끼임의 ', check: true, require: false },
                { index: 18, text: '사고가 ', check: true, require: false },
                { index: 19, text: '있었습니다.', check: true, require: false },
                { index: 0, text: '<br/><br/>', check: true, require: false },
                { index: 20, text: '아웃 ', check: true, require: false },
                { index: 21, text: '트리거 ', check: true, require: false },
                { index: 22, text: '조작 ', check: true, require: false },
                { index: 23, text: '시 ', check: true, require: false },
                { index: 24, text: '2인 ', check: false, require: true },
                { index: 25, text: '1조로 ', check: false, require: true },
                { index: 26, text: '진행해 ', check: true, require: false },
                { index: 27, text: '주시고, ', check: true, require: false },
                { index: 28, text: '고임목 ', check: true, require: false },
                { index: 29, text: '설치 ', check: true, require: false },
                { index: 30, text: '및 ', check: true, require: false },
                { index: 31, text: '철거는 ', check: true, require: false },
                { index: 32, text: '꼭 ', check: true, require: false },
                { index: 33, text: '핸드 ', check: false, require: true },
                { index: 34, text: '스틱 ', check: false, require: true },
                { index: 35, text: '등 ', check: true, require: false },
                { index: 36, text: '공구를 ', check: true, require: false },
                { index: 37, text: '활용해야 ', check: true, require: false },
                { index: 38, text: '합니다.', check: true, require: false },
                { index: 0, text: '<br/><br/>', check: true, require: false },
                { index: 39, text: '또한, ', check: true, require: false },
                { index: 40, text: '작업 ', check: true, require: false },
                { index: 41, text: '시 ', check: true, require: false },
                { index: 42, text: '운전자는 ', check: true, require: false },
                { index: 43, text: '브레이크 ', check: false, require: true },
                { index: 44, text: '밟기를 ', check: true, require: false },
                { index: 45, text: '꼭 ', check: true, require: false },
                { index: 46, text: '유지해 ', check: true, require: false },
                { index: 47, text: '주시기 ', check: true, require: false },
                { index: 48, text: '바랍니다.', check: true, require: false },
            ],
            searchKeywords: [
                { matchIndex: 3, texts: ['중점']},
                { matchIndex: 4, texts: ['관리']},
                { matchIndex: 5, texts: ['사항은']},
                { matchIndex: 6, texts: ['차량']},
                { matchIndex: 7, texts: ['끼임', '게임']},
                { matchIndex: 10, texts: ['고임목', '고인목']},
                { matchIndex: 12, texts: ['아웃']},
                { matchIndex: 13, texts: ['트리거']},
                { matchIndex: 24, texts: ['2인', '이인']},
                { matchIndex: 25, texts: ['1조로', '일조로']},
                { matchIndex: 33, texts: ['핸드']},
                { matchIndex: 34, texts: ['스틱']},
                { matchIndex: 43, texts: ['브레이크']},
            ],
            voiceState: 'start',
        }
    },
    computed: {
        progress() {
            return Number((this.step) / (this.totalStep)) * 100;
        },
        allConfirm() {
            return this.voiceState === 'finish'
        }
    },
    methods: {
        onForceNext() {
            this.$router.push('/TBM/Step06')
        },
        onClickNext() {
            if (this.allConfirm) {
                this.$router.push('/TBM/Step06')
            } else if (this.voiceState === 'replay') {
                this.$refs.VoiceRecorder.startRecording()
            }
        },
        checkAnalysisResult(result) {
            const audio = result.replace(/\s/g, '');

            for (const keyword of this.searchKeywords) {
                for (const text of keyword.texts) {
                    if (audio.includes(text)) {
                        for (const operator of this.textArray) {
                            if (operator.index === keyword.matchIndex) {
                                operator.check = true
                                break
                            }
                        }
                    }
                }
            }
        },
        midResultState(result) {
            if (result === 'finish') {
                const allChecked = this.textArray.every((operator) => operator.check);
                if (!allChecked) {
                    result = 'replay'
                    this.$refs.VoiceRecorder.changeState(result)
                }
            }
            console.log('result', result)
            this.voiceState = result
        },
        textStyle(item) {
            if (item.require) {
                if (item.check) {
                    return 'color_primary_4 font_extra_bold'
                } else {
                    return 'color_primary_4'
                }
            } else {
                return 'color_gray_4'
            }
        }
    }
}
</script>

