        line-height: 1.43;
        word-break: keep-all;
    }
    &-notice {
        &-box {
            border-radius: 6px;
            background-color: #eee;
            padding: 7px;
            font-family: 'Pretendard Medium';
            font-size: 14px;
            margin-bottom: 4px;
            color: #777e8c;
            display: flex;
            align-items: center;
        }
        &-icon {
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }
    }
    &-content {
        font-family: 'Pretendard';
        font-size: 24px;
        line-height: 1.5;
        letter-spacing: -0.5px;
        text-align: left;
        color: #777e8c;
        word-break: keep-all;
    }
}

</style>